<template>
    <main class="content container">
        <div class="content__top">
            <ul class="breadcrumbs">
                <li class="breadcrumbs__item">
                    <router-link class="breadcrumbs__link" :to="{name: 'products'}">
                        
                    </router-link>
                </li>
                <li class="breadcrumbs__item">
                    <router-link class="breadcrumbs__link" :to="{name: 'products'}">
                        {{currentCategoty.title}}
                    </router-link>
                </li>
                <li class="breadcrumbs__item">
                    <a class="breadcrumbs__link">
                        {{currentProduct.title}}
                    </a>
                </li>
            </ul>
        </div>
        <div v-if="loadingFailed">   </div>
        <Loader v-if="loading"/>
        <section v-if="!loadingFailed && !loading" class="item">
            <div class="item__pics pics">
                <div class="pics__wrapper">
                    <img width="570" height="570" :src="currentProduct.image"
                         :alt="currentProduct.title">
                </div>
            </div>

            <div class="item__info">
                <span class="item__code">: {{currentProduct.id}}</span>
                <h2 class="item__title">
                    {{currentProduct.title}}
                </h2>
                <div class="item__form">
                    <form class="form" action="#" @submit.prevent="addToCart" method="POST">
                        <b class="item__price">
                            {{currentProduct.price | numberFormat}} 
                        </b>

                        <fieldset class="form__block">
                            <legend class="form__legend">:</legend>
                            <ul class="colors" v-if="currentProduct.colors">
                                <li class="colors__item" v-for="color in currentProduct.colors" :key="color">
                                    <label class="colors__label">
                                        <input class="colors__radio sr-only" type="radio" name="color-item"
                                               :value="color"
                                               v-model="currentColor">
                                        <span class="colors__value" :style="{'background-color': color}">
                    </span>
                                    </label>
                                </li>
                            </ul>
                        </fieldset>

                        <fieldset class="form__block">
                            <legend class="form__legend">  :</legend>

                            <ul class="sizes sizes--primery">
                                <li class="sizes__item">
                                    <label class="sizes__label">
                                        <input class="sizes__radio sr-only" type="radio" name="sizes-item" value="32">
                                        <span class="sizes__value">
                      32gb
                    </span>
                                    </label>
                                </li>
                                <li class="sizes__item">
                                    <label class="sizes__label">
                                        <input class="sizes__radio sr-only" type="radio" name="sizes-item" value="64">
                                        <span class="sizes__value">
                      64gb
                    </span>
                                    </label>
                                </li>
                                <li class="sizes__item">
                                    <label class="sizes__label">
                                        <input class="sizes__radio sr-only" type="radio" name="sizes-item" value="128"
                                               checked="">
                                        <span class="sizes__value">
                      128gb
                    </span>
                                    </label>
                                </li>
                            </ul>
                        </fieldset>

                        <div class="item__row">
                            <div class="form__counter">
                                <Counter iconWidth="12" iconHeight="12" v-model="productAmount"/>
                            </div>

                            <button class="button button--primery" type="submit" :disabled="productAddSending">
                                {{buttonText}}
                            </button>
                        </div>
                    </form>
                </div>
            </div>

            <div class="item__desc">
                <ul class="tabs">
                    <li class="tabs__item">
                        <a class="tabs__link tabs__link--current">
                            
                        </a>
                    </li>
                    <li class="tabs__item">
                        <a class="tabs__link" href="#">
                            
                        </a>
                    </li>
                    <li class="tabs__item">
                        <a class="tabs__link" href="#">
                            
                        </a>
                    </li>
                    <li class="tabs__item">
                        <a class="tabs__link" href="#">
                              
                        </a>
                    </li>
                </ul>

                <div class="item__content">
                    <p>
                         GPS, , BEIDOU Galileo  QZSS<br>
                          <br>
                          Bluetooth Smart, ANT+  Wi-Fi<br>
                          <br>
                    </p>

                    <a href="#">
                         
                    </a>

                    <h3> ?</h3>

                    <p>
                        Wahoo ELEMNT BOLT GPS   ,     ,
                           . Wahoo ELEMNT BOLT GPS     ANT+,
                            .    ,    
                        .          ,  
                           .    ,
                             .      
                            .
                    </p>

                    <h3></h3>

                    <p>
                         Wahoo ELEMNT BOLT  .     74,6 x
                        47,3 x 22,1 .     .     
                        .         56 .  
                           ,        
                        : ,   ,   .. (    
                        ).        IPX7.  , 
                           ,     ( 30 )   
                            1 .
                    </p>
                </div>
            </div>
        </section>
    </main>
</template>

<script>

    import goToPage from "@/helpers/goToPage";
    import numberFormat from "@/helpers/numberFormat";
    import axios from 'axios'
    import {BASE_URL} from "@/helpers/config";
    import {mapActions} from 'vuex'
    import Loader from "../components/UI/Loader/Loader";

    import Counter from "../components/UI/Counter/Counter";

    export default {
        name: "ProductPage",
        components: {Counter, Loader},
        data() {
            return {
                currentColor: '',
                productAmount: 1,
                productData: null,
                loading: false,
                loadingFailed: false,
                productAdded: false,
                productAddSending: false,

            }
        },
        filters: {
            numberFormat
        },
        computed: {
            buttonText() {
                if (!this.productAddSending && !this.productAdded){
                    return ' '
                } else if (this.productAddSending && !this.productAdded){
                    return ''
                } else {
                    return ''
                }
            },
            currentProduct() {
                return this.productData ?
                    {
                        ...this.productData,
                        image: this.productData.image.file.url,
                        colors: this.productData.colors.map(color => color.code)
                    }
                    : {}
            },
            currentCategoty() {
                return this.productData ? this.productData.category : '';
            },
        },
        methods: {
            ...mapActions(['addProductToCart']),
            goToPage,
            addToCart() {
                this.productAdded = false;
                this.productAddSending = true;
                const order = {
                    productId: this.currentProduct.id,
                    amount: this.productAmount
                }
                this.addProductToCart(order)
                .then(() => {
                    this.productAdded = true;
                    this.productAddSending = false;
                })
            },
            getProduct() {
                this.loading = true;
                axios.get(BASE_URL + `/products/${this.$route.params.id}`)
                    .then(response => {
                        this.productData = response.data
                    })
                    .catch(() => this.loadingFailed = true)
                    .then(() => this.loading = false)
            },
            subAmount() {
                if (this.productAmount > 1)
                    this.productAmount -= 1
                else
                    this.productAmount = 0
            },
            incAmount() {
                this.productAmount += 1
            }
        },
        watch: {
            '$route.params.id': {
                handler() {
                    this.getProduct()
                },
                immediate: true
            }
        },

    }
</script>

